.container {
  width: 100%;
  max-width: 375px;
  padding: 0 20px;
  margin: 0 auto;
  /* position: relative; */
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1184px) {
  .container {
    max-width: 1440px;
    padding: 0 84px;
  
  }
}
